.search-container {
  height: 44px;
  background-color: #0074d9;
  justify-content: center;
  margin-bottom: 20px;
  display: flex;
}

.search-form {
  align-items: center;
  display: flex;
}

#search-input {
  border-color: #0074d9;
  border-radius: 5px 0 0;
  padding: 10px;
}

button[type="submit"] {
  cursor: pointer;
  background-color: #0074d9;
  border: none;
  border-radius: 5px;
  padding: 8px;
}

button[type="submit"]:hover, button[type="submit"]:focus {
  fill: #000;
  background-color: rgba(0, 0, 0, .204);
}

.gallery {
  grid-gap: 20px;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  margin-left: 15px;
  margin-right: 15px;
  display: grid;
}

.card {
  position: relative;
}

.card img {
  width: 100%;
  height: 268px;
  border-radius: 5px 0 0 5px;
  box-shadow: 3px 3px 8px rgba(0, 0, 0, .3);
}

.card-info {
  color: #fff;
  width: 100%;
  background-color: rgba(0, 0, 0, .7);
  position: absolute;
  bottom: 0;
  left: 0;
}

.card-info ul {
  justify-content: space-between;
  margin: 0;
  padding: 5px;
  display: flex;
}

.card-info li {
  flex-direction: column;
  align-items: center;
  display: flex;
}

.card-info li:first-child {
  margin-bottom: 0;
}

.card-info li i {
  margin-right: 5px;
}

